<template>
  <div class="TopUp-box" @click="getpay(id,type)">
    <h4>{{ title }}</h4>
    <slot> </slot>
    <h6>￥{{ money }}</h6>
  </div>
</template>

<script>
export default {
  name: "TopUp",
  props: ["title", "money",'id','type'],
  methods:{
    getpay(id, typr) {
      this.axios
        .get(
          `https://jyapi.jyacg.com/web/pay?goods_id=${id}&type=${typr}&pay_way=2`,
          {
            headers: {
              authorization: localStorage.getItem("api_token"),
            },
          }
        )
        .then(({ data }) => {
            window.location.href =data.data.qr
        });
    },
  }
};
</script>

<style lang="scss" scoped>
.TopUp-box{
    overflow: hidden;
    width: 92px;
    display: flex;
    border: 1px solid #77afe9;
    border-radius: 10px;
    flex-direction: column;
    text-align: center;
    h4{
        margin: 10px 0;
        font-weight: 900;
        font-size: 14px;
        width: 100%;
    }
    span{
        font-size: 13px;
        display: block;
        height: 16px;
    }
    h6{ 
        width: 100%;
        height: 30px;
        line-height: 39px;
        font-size: 16px;
        color: #fff;
        font-weight: 900;
        margin-top: 10px;
        background-color: #77afe9;
    }
}
</style>