<template>
  <div class="topup">
    <Titletop title="充值中心"
      ><router-link slot="l" :to="{ name: 'mineself' }"></router-link>
      <router-link slot="r" to="index/"></router-link
    ></Titletop>
    <div class="topuplist">
      <div></div>
      <div class="Viplist">
        <TopUp
          v-for="item in Viplist"
          :key="item.id"
          :title="item.goods_name"
          :money="item.price"
          :id="item.id"
          type="1"
          ><span>{{ item.member_day }}天</span></TopUp
        >
      </div>

      <div class="Bookcoin">
        <TopUp
          v-for="item in Bookcoin"
          :key="item.id"
          :title="item.goods_name"
          :money="item.price"
          :id="item.id"
          type="2"
          ><span style="color: #999">{{ item.book_coins }}书币</span>
          <span style="color: #999">赠{{ item.give_away_coins }}书币</span>
        </TopUp>
      </div>
    </div>
  </div>
</template>

<script>
import Titletop from "@/components/Titletop.vue";
import TopUp from "@/components/TopUp.vue";
export default {
  name: "topup",
  components: { Titletop, TopUp },
  data() {
    return {
      Viplist: [],
      Bookcoin: [],
    };
  },
  mounted() {
    this.axios
      .get("https://jyapi.jyacg.com/admin/user/buycenter", {
        headers: {
          authorization: localStorage.getItem("api_token"),
        },
      })
      .then(({ data }) => {
        this.Bookcoin = data.data.Bookcoin;
        this.Viplist = data.data.Viplist;
      });
  },
};
</script>

<style lang="scss" scoped>
.topup {
  background-color: #b9dafc;
  min-height: 700px;

  .topuplist {
    width: 330px;
    margin: 25px 12.6px;
    padding: 30px 10px 0px;
    background-color: #fff;
    border-radius: 20px;
    .Bookcoin,
    .Viplist {
      display: flex;
      margin-top: 20px;
      flex-wrap: wrap;
      div {
        margin: 0px 8px 20px;
      }
    }
  }
}
</style>